const cards = document.querySelectorAll('.apartments__item');

for (let card of cards) {
  const button = card.querySelector('.apartments__button');
  const div = document.createElement('div');
  button.addEventListener('click', (evt) => {
    evt.stopPropagation();


    evt.stopPropagation();
    const a = document.createElement('a');
    const b = document.createElement('b');
    const span = document.createElement('span');

    a.innerHTML = 'оплате';
    a.style.color = '#FF4B77';
    a.style.fontSize = '14px';
    a.style.fontWeight = '600';
    a.style.lineHeight = '21px';
    a.href = '#';

    b.innerHTML = 'Номер зарезервирован';
    b.style.margin = '0';
    b.style.fontSize = '14px';
    b.style.fontWeight = '600';
    b.style.lineHeight = '21px';
    b.style.color = '#fff';
    b.style.width = 'max-content';
    b.style.display = 'block';
    b.style.margin = '0 auto';

    span.innerHTML = 'Перейти к ';
    span.style.fontSize = '14px';
    span.style.fontWeight = '600';
    span.style.lineHeight = '21px';
    span.style.color = '#fff';

    div.style.backgroundColor = '#3C3E43';
    div.style.width = '100%';
    div.style.position = 'absolute';
    div.style.bottom = '0';
    div.style.left = '0';
    div.style.textAlign = 'center';
    div.style.borderRadius = '0 0 10px 10px';
    div.style.padding = '25px 0 29px 0';
    if (window.innerWidth <= 1280) {
      div.style.padding = '50px 0 54px 0';
    }

    if (window.innerWidth <= 768) {
      div.style.padding = '50px 0 54px 0';
    }

    div.insertAdjacentElement('afterbegin', a);
    div.insertAdjacentElement('afterbegin', span);
    div.insertAdjacentElement('afterbegin', b);

    card.classList.add('apartments__item--disabled');

    card.append(div);
  });
  card.addEventListener('click', () => {
    card.classList.remove('apartments__item--disabled');
    div.innerHTML = '';
    div.style.padding = '0';
  });
}
